body {
  font-family: calibri-light, calibri, sans-serif;
  letter-spacing: 1px;
  font-size: 14px;
  padding: 20px;
  width: 50%;
  height: 50%;
  margin: auto;
}

input,
select,
textarea,
button {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  letter-spacing: inherit;
}

#chat-box {
  position: absolute;
  bottom: 20px;
}
@media only screen and (max-device-width: 480px)   {    
  body { width:90%;}
  #Chat-Container {
    min-height:90vh;
    max-height:90vh
  }
}

#ChatboxApp {
  height:50%
}
#chat-user {
  width: 200px;
  height: 26px;
  background-color: transparent;
  border: 2px solid #cdcdcd;
  border-radius: 20px;
  color: #cdcdcd;
  padding-left: 10px;
  padding-right: 10px;
  -webkit-transition: border .5s ease, color .5s ease;
  transition: border .5s ease, color .5s ease;
}

#chat-user:focus,
#chat-user:hover {
  border: 2px solid #69D2E7;
  color: #000;
  outline: 0;
}

#chat-submit {
  cursor: pointer;
  text-align: center;
  background-color: transparent;
  border: 2px solid #cdcdcd;
  color: #cdcdcd;
  border-radius: 20px;
  -webkit-transition: border .5s ease, color .5s ease;
  transition: border .5s ease, color .5s ease;
}
::-webkit-scrollbar {
  width: 0px;
  background: transparent; /* make scrollbar transparent */
}

#chat-submit:hover,
#chat-submit:focus {
  border: 2px solid #69D2E7;
  color: #69D2E7;
  outline: 0;
}

.dialogue {
  min-width: 10%;
  max-width: 70%;
  border: 1px solid #cdcdcd;
  border-radius: 20px;
  padding: 5px 10px;
  margin-bottom: 10px;
  clear: both;
}

.dialogue-bot {
  background-color: #fcfcfc;
  float: left;
}

.dialogue-user {
  background-color: #00A4FF;
  float: right;
}
.product-image {
  min-width: 10%;
  max-width: 70%;
}
.response-buttons ul {
  display: inline;
   list-style-type: none;
}
